@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter.ttf");
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-VariableFont_wght.ttf");
  font-style: normal;
}
.footer {
  display: none;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 80px;
  background-color: rgb(255, 255, 255);
  border-top: 1px solid rgba(0, 0, 0, 0.16);
  padding: 0 24px;
  align-items: center;
}
@media (max-width: 1199px) {
  .footer {
    display: flex;
  }
}
@media (max-width: 767px) {
  .footer {
    height: 76px;
    padding: 0 16px;
  }
}
.footer__nav-list {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 12px;
  padding: 0;
  margin: 0;
  list-style: none;
}

.footer-nav-element__link {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
  gap: 8px;
  height: 52px;
  padding: 0 12px;
  border-radius: 2px;
  text-decoration: none;
  font-size: 18px;
  font-weight: 600;
  line-height: 28px;
  color: #2f2227;
  transition: background-color 0.2s;
}
@media (max-width: 767px) {
  .footer-nav-element__link {
    gap: 4px;
    height: 60px;
    flex-direction: column;
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
  }
}
.footer-nav-element__link_active {
  background-color: rgba(0, 0, 0, 0.08);
}
@media (max-width: 767px) {
  .footer-nav-element__link_active {
    background-color: transparent;
    color: #cc4b00;
  }
  .footer-nav-element__link_active .footer-nav-element {
    color: #cc4b00;
  }
}
.footer-nav-element__link:hover:not(.footer-nav-element__link_active) {
  background-color: rgba(0, 0, 0, 0.04);
  color: #ff5e00;
}
.footer-nav-element__link:active:not(.footer-nav-element__link_active) {
  background-color: #993800;
  color: #fff;
}
.footer-nav-element__link:active:not(.footer-nav-element__link_active) .footer-nav-element__icon {
  color: #fff;
}
.footer-nav-element__icon {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
  height: 84px;
  display: flex;
  align-items: center;
  background-color: rgb(255, 255, 255);
}
.header__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 0 24px;
}
@media (max-width: 767px) {
  .header__content {
    padding: 0 16px;
  }
}
.header__logo {
  width: 241px;
  height: 40px;
}
.header__logo img {
  width: 100%;
}
.header__button {
  flex-shrink: 0;
  width: 48px;
  height: 48px;
}
.header__button-icon {
  width: 32px;
  height: 32px;
}

.nav {
  display: flex;
  margin-left: 64px;
  margin-right: auto;
}
@media (max-width: 1199px) {
  .nav {
    display: none;
  }
}
.nav__list {
  display: flex;
  flex-wrap: nowrap;
  list-style: none;
  padding: 0;
  margin: 0;
  gap: 12px;
}

.nav-element__link {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
  gap: 8px;
  height: 52px;
  padding: 0 12px;
  border-radius: 2px;
  text-decoration: none;
  font-family: "Montserrat", "Helvetica", sans-serif;
  font-size: 18px;
  font-weight: 600;
  line-height: 28px;
  color: #2f2227;
  transition: background-color 0.2s;
}
.nav-element__link_active {
  background-color: rgba(0, 0, 0, 0.08);
}
.nav-element__link:hover:not(.nav-element__link_active) {
  background-color: rgba(0, 0, 0, 0.04);
  color: #ff5e00;
}
.nav-element__link:active:not(.nav-element__link_active) {
  background-color: #993800;
  color: #fff;
}
.nav-element__link:active:not(.nav-element__link_active) .nav-element__icon {
  color: #fff;
}
.nav-element__icon {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  color: rgba(47, 34, 39, 0.8);
}

.select-component-wrapper {
  position: relative;
}
.select-component-wrapper .select-component {
  position: relative;
  min-width: 124px;
  padding: 0 42px 0 14px;
  height: 44px;
  display: flex;
  align-items: center;
  border-radius: 2px;
  border: 2px solid rgba(0, 0, 0, 0.16);
  background: rgb(255, 255, 255);
  transition: border-color 0.2s;
}
.select-component-wrapper .select-component:hover {
  border-color: rgba(0, 0, 0, 0.24);
}
.select-component-wrapper .select-component:focus {
  outline: none;
  box-shadow: none;
}
.select-component-wrapper .select-component_selected {
  border-bottom-color: #2f2227;
  background-color: #2f2227;
}
.select-component-wrapper .select-component_selected .ss-values {
  color: #fff !important;
}
.select-component-wrapper .select-component_selected ~ .select-component__reset {
  color: rgba(255, 255, 255, 0.72);
}
.select-component-wrapper .select-component.ss-disabled {
  border-color: transparent;
  background-color: rgba(0, 0, 0, 0.04);
}
.select-component-wrapper .select-component.ss-disabled .ss-values {
  color: rgba(47, 34, 39, 0.8);
}
.select-component-wrapper .select-component.ss-disabled .ss-arrow path {
  fill: rgba(47, 34, 39, 0.8);
}
.select-component-wrapper .select-component__label {
  margin-bottom: 4px;
  color: rgba(47, 34, 39, 0.8);
  font-size: 14px;
  line-height: 20px;
}
.select-component-wrapper .select-component__reset {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 22px;
  height: 34px;
  top: 5px;
  right: 12px;
  padding: 0;
  border: none;
  cursor: pointer;
  background-color: transparent;
}
.select-component-wrapper .select-component__reset-icon {
  width: 16px;
  height: 16px;
  fill: #8f8f8f;
}
.select-component-wrapper .select-component .ss-values {
  color: #1f1f1f;
  font-size: 14px;
  line-height: 20px;
}
.select-component-wrapper .select-component .ss-values .ss-placeholder,
.select-component-wrapper .select-component .ss-values .ss-single {
  padding: 0;
  margin: 0;
  font-size: 16px;
  line-height: 24px;
}
.select-component-wrapper .ss-open-below {
  border-color: #76a6f0;
  border-radius: 2px 2px 0 0;
}
.select-component-wrapper .ss-open-below:hover {
  border-color: #76a6f0;
}
.select-component-wrapper .ss-arrow {
  position: absolute;
  right: 12px;
  width: 16px;
  height: 16px;
  margin: 0;
}
.select-component-wrapper .ss-arrow path {
  stroke: none;
  fill: #2f2227;
}

.ss-content {
  margin-top: 0 !important;
  padding: 4px;
  border-radius: 2px;
  border-bottom-right-radius: 2px !important;
  border-bottom-left-radius: 2px !important;
  background: rgb(255, 255, 255);
  box-shadow: 0px 16px 24px 0px rgba(0, 0, 0, 0.06), 0px 2px 6px 0px rgba(0, 0, 0, 0.04), 0px 0px 1px 0px rgba(0, 0, 0, 0.04);
}
.ss-content .ss-search {
  margin-bottom: 4px;
  padding: 0;
}
.ss-content .ss-search input {
  height: 44px;
  padding: 12px 21px 12px 40px;
  border-radius: 0;
  box-shadow: none;
  border: none;
  border-bottom: 2px solid rgba(0, 0, 0, 0.16);
  background-color: rgb(255, 255, 255);
  font-size: 14px;
  line-height: 20px;
  transition: all 0.2s;
}
.ss-content .ss-search input::placeholder {
  color: rgba(47, 34, 39, 0.56);
}
.ss-content .ss-search input:hover {
  background-color: rgba(0, 0, 0, 0.04);
}
.ss-content .ss-search input:focus {
  outline: none;
  border-color: #1a6be6;
  background-color: rgb(255, 255, 255);
  box-shadow: none;
}
.ss-content .ss-search input::-webkit-search-cancel-button {
  display: none;
}
.ss-content .ss-list .ss-search {
  font-size: 14px;
  line-height: 20px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.ss-content .ss-option:not(.ss-hide) {
  display: block !important;
  border-radius: 2px;
  padding: 14px 16px !important;
  color: #2f2227 !important;
  font-size: 14px;
  line-height: 20px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.ss-content .ss-option:not(.ss-hide):hover {
  background-color: #e8f0fd !important;
}
.ss-content .ss-option:not(.ss-hide).ss-selected {
  background-color: #e8f0fd !important;
}
.ss-content .ss-list::-webkit-scrollbar {
  width: 4px;
}
.ss-content .ss-list::-webkit-scrollbar-thumb {
  border-radius: 10px;
  border: 2px solid rgba(0, 0, 0, 0.16);
}
.ss-content .ss-list::-webkit-scrollbar-track {
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
}

.pagination {
  display: flex;
  flex-wrap: nowrap;
  gap: 4px;
  margin-left: auto;
  margin-right: auto;
}
.pagination__button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: none;
  border-radius: 2px;
  border: 2px solid transparent;
  background-color: transparent;
  width: 48px;
  height: 48px;
  cursor: pointer;
  color: #1556b8;
  text-align: center;
  font-size: 16px;
  line-height: 24px;
  transition: all 0.2s;
}
.pagination__button:hover:not(.pagination__button_disabled, .pagination__button_active) {
  border-color: #1a6be6;
}
.pagination__button_active {
  color: #2f2227;
  background-color: rgba(0, 0, 0, 0.08);
}
.pagination__button_disabled {
  opacity: 0.48;
  cursor: default;
}
.pagination__icon {
  width: 24px;
  height: 24px;
  fill: currentColor;
}
.pagination__icon_right {
  transform: rotate(180deg);
}
@media (max-width: 767px) {
  .pagination__button_desktop {
    display: none;
  }
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 30px;
  margin: 0;
  border-width: 2px;
  border-style: solid;
  border-radius: 2px;
  font-family: "Montserrat", "Helvetica", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 600;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.2s;
}
.button.primary {
  border-color: #ff5e00;
  background-color: #ff5e00;
  color: #fff;
}
.button.primary:hover:not(:disabled) {
  border-color: #cc4b00;
  background-color: #cc4b00;
}
.button.primary:active:not(:disabled) {
  border-color: #993800;
  background-color: #993800;
}
.button.primary:disabled {
  opacity: 0.48;
}
.button.secondary {
  background-color: transparent;
  border-color: #ff5e00;
  color: #2f2227;
}
.button.secondary:hover:not(:disabled) {
  border-color: #cc4b00;
  background-color: rgba(0, 0, 0, 0.04);
}
.button.secondary:active:not(:disabled) {
  border-color: #993800;
  background-color: #993800;
  color: #fff;
}
.button.secondary:disabled {
  opacity: 0.48;
}

.button-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0;
  border: none;
  border-radius: 2px;
  background-color: transparent;
  color: #2f2227;
  cursor: pointer;
  transition: all 0.2s;
}
.button-icon:focus {
  outline: none;
}
.button-icon:hover {
  background-color: rgba(0, 0, 0, 0.04);
  color: #ff5e00;
}
.button-icon:active {
  background-color: #993800;
  color: #fff;
}

.text-input {
  position: relative;
  display: flex;
  flex-direction: column;
}
.text-input_with-icon .text-input__input {
  padding-left: 48px;
}
.text-input__label {
  margin-bottom: 4px;
  margin-left: 2px;
  font-size: 14px;
  line-height: 20px;
  color: rgba(47, 34, 39, 0.8);
}
.text-input__input {
  min-height: 48px;
  padding: 10px 34px 10px 16px;
  border-radius: 4px;
  border: 2px solid rgba(0, 0, 0, 0.16);
  background-color: rgb(255, 255, 255);
  color: inherit;
  font-family: inherit;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  transition: all 0.2s;
}
.text-input__input::placeholder {
  color: rgba(47, 34, 39, 0.56);
}
.text-input__input:hover {
  border-color: rgba(0, 0, 0, 0.24);
}
.text-input__input:focus {
  outline: none;
  border-color: #76a6f0;
}
.text-input__input:disabled {
  border-color: rgba(0, 0, 0, 0.08);
  background-color: rgba(0, 0, 0, 0.08);
}
.text-input__input.error {
  border-color: #ff5459;
}
.text-input__input_force-alert {
  border-color: #ff5459;
}
.text-input__input-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
}
.text-input__reset {
  display: none;
}
.text-input__reset.active {
  display: flex;
}
.text-input__show {
  display: flex;
}
.text-input__show.shift {
  right: 40px;
}
.text-input__reset, .text-input__show {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 12px;
  width: 24px;
  height: 24px;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.text-input__reset:hover svg, .text-input__show:hover svg {
  color: #2f2227;
}
.text-input__reset.active, .text-input__show.active {
  display: flex;
}
.text-input__icon {
  color: #2f2227;
  transition: all 0.2s;
}
.text-input__lens-icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 12px;
  width: 24px;
  height: 24px;
  color: rgba(47, 34, 39, 0.8);
}
.text-input__alert {
  display: none;
  margin-top: 4px;
  margin-left: 2px;
  color: #ff5459;
  font-size: 14px;
  line-height: 20px;
}
.text-input__alert.active {
  display: block;
}
.text-input__addon {
  margin-top: 4px;
  font-size: 14px;
  line-height: 20px;
}
.text-input__sub-text {
  margin-top: 4px;
  margin-left: 2px;
  color: #fff;
  font-size: 14px;
  line-height: 20px;
}
.text-input__force-alert {
  margin-top: 4px;
  margin-left: 2px;
  color: #ff5459;
  font-size: 14px;
  line-height: 20px;
}

.text-area {
  position: relative;
  display: flex;
  flex-direction: column;
}
.text-area__label {
  margin-bottom: 4px;
  margin-left: 2px;
  color: rgba(47, 34, 39, 0.8);
  font-size: 14px;
  line-height: 20px;
}
.text-area__input {
  resize: none;
  height: 144px;
  padding: 12px;
  border-radius: 4px;
  border: 2px solid rgba(0, 0, 0, 0.16);
  background: rgb(255, 255, 255);
  color: #2f2227;
  font-family: inherit;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  transition: all 0.2s;
}
.text-area__input::placeholder {
